import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import OrderImage from './OderImage';
import SelectCustom from '../../../components/common/SelectCustom';
import TextFieldLite from '../../../components/common/TextFieldLite';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import IconButton from '@material-ui/core/IconButton';

const MakeOrderMakeOrder = () => {
  const [product, setProduct] = useState({
    type: '',
    filling: '',
    extraExpenses: '',
  });

  const [countPrice, setCountPrice] = useState(false);
  const [priceSecond, setPriceSecond] = useState(false);
  const [countWeight, setCountWeight] = useState(false);
  const [countExpense, setCountExpense] = useState(false);
  const [showCountAddInfo, setShowCountAddInfo] = useState(false);

  const countPriceHandler = (event) => {
    setCountPrice(event.target.checked);
  };

  const countWeightHandler = (event) => {
    setCountWeight(event.target.checked);
  };

  const countExpenseHandler = (event) => {
    setCountExpense(event.target.checked);
  };

  const showCountAddInfoHandler = () => {
    setShowCountAddInfo(!showCountAddInfo)
  };

  const [productTypesAll, setProductTypesAll] = useState({
    ten: 10,
    twenty: 20,
    thirty: 30,
  });

  const [productFilling, setProductFilling] = useState({
    Сливочный: 10,
    Кремовый: 20,
    Шоколадный: 30,
  });

  const [extraExpenses, setExtraExpenses] = useState({
    Кробка: 10,
    Пачка: 20,
    Сумка: 30,
  });

  const classes = useStyles();

  const onChangeProductType = (event) => {
    const name = event.target.name;
    setProduct({
      ...product,
      [name]: event.target.value,
    });
  };

  return (
    <div className={classes.makeOrderBlock}>
      <div className={classes.makeOrderBlockLineOne}>
        <OrderImage width='100px' height='100px'/>
        <div className={classes.productTypeBlock}>
          <SelectCustom optionsObject={productTypesAll}
                        label='Вид'
                        name='type'
                        onChangeFunction={onChangeProductType}
                        val={product.type}/>
          <div className={classes.productTypeText}>
            Здесь будет небольшое описание позиции. В общих чертах. Важные
            моменты -
            цветовая гамма, как подписать и тд
          </div>
        </div>
        <div className={classes.createProductFirstLine}>
          <div className={classes.createProductFirstLineResult}>
            <div className={classes.resFirstLineRedDiv}>Заработок</div>
            <div className={classes.resFirstLineRedDiv}>2000р</div>
            <div>(Ф800р)</div>
            <div>1500р/кг</div>
          </div>
          <div className={classes.productPriceWrapper}>
            <TextFieldLite label="Цена"
                           required
                           size="small"
                           name="price"
                           variant="outlined"
                           value={''}
                           className={classes.productPrice}
                           onChange={() => {
                           }}
                           type='number'/>

            <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                  checked={countPrice}
                  onChange={countPriceHandler}
                  name="countPrice"
                />
              }
              label="Рассчитать"
            />
          </div>

          <div className={classes.productWeightWrapper}>
            <TextFieldLite label="Вес"
                           required
                           size="small"
                           name="weight"
                           variant="outlined"
                           value={''}
                           className={classes.productWeight}
                           onChange={() => {
                           }}
                           type='number'/>
            <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                  checked={countWeight}
                  onChange={countWeightHandler}
                  name="countWeight"
                />
              }
              label="Рассчитать"
            />

          </div>
          <div className={classes.productExpenseWrapper}>
            <TextFieldLite label="Расход"
                           required
                           size="small"
                           name="expense"
                           variant="outlined"
                           value={''}
                           className={classes.productExpense}
                           onChange={() => {
                           }}
                           type='number'/>
            <FormControlLabel
              className={classes.countCheckbox}
              labelPlacement='start'
              control={
                <PurpleCheckbox
                  checked={countExpense}
                  onChange={countExpenseHandler}
                  name="countExpense"
                />
              }
              label="Рассчитать"
            />
          </div>

        </div>
      </div>
      <div className={classes.makeOrderBlockLineTwo}>
        <div className={classes.makeOrderBlockLineTwoLeft}>
          <div className={classes.makeOrderBlockLineTwoLeftOne}>
            <div className={classes.makeOrderBlockLineTwoLeftOneLineFakeOne}>
            </div>
            <div className={classes.makeOrderBlockLineTwoLeftOneLineWeightLine}>
              <TextFieldLite label="Вес украшений"
                             required
                             size="small"
                             name="decoration_weight"
                             variant="outlined"
                             value={''}
                             className={classes.decorationWeight}
                             onChange={() => {
                             }}
                             type='number'/>
            </div>
            <div className={classes.makeOrderBlockLineTwoLeftOneLineFakeTwo}>
            </div>
          </div>
          <div className={classes.makeOrderBlockLineTwoLeftTwo}>
            <div className={classes.makeOrderBlockLineTwoSecondTitle}>
              Упаковка
            </div>
            <div className={classes.boxSizeWrapper}>
              <TextFieldLite label="Коробка"
                             required
                             size="small"
                             name="box_size"
                             variant="outlined"
                             value={''}
                             className={classes.boxSizeField}
                             onChange={() => {
                             }}
                             type='number'/>
            </div>
            <div className={classes.substrateWrapper}>
              <TextFieldLite label="Подложка"
                             required
                             size="small"
                             name="substrate"
                             variant="outlined"
                             value={''}
                             className={classes.substrateField}
                             onChange={() => {
                             }}
                             type='number'/>
            </div>
          </div>
          <div className={classes.makeOrderBlockLineTwoLeftThree}>
            <div className={classes.makeOrderBlockLineTwoThirdTitle}>
              Доп  расходы
            </div>
            <div className={classes.extraExpensesTitleWrapper}>
              <SelectCustom optionsObject={extraExpenses}
                            label='Название'
                            onChangeFunction={onChangeProductType}
                            val={product.extraExpenses}
                            name='extraExpenses'
                            className={classes.extraExpensesTitle}/>
              <IconButton className={classes.addButtonDesktop}>
                <PlusRoundButton addLine={() => {
                }}/>
              </IconButton>
            </div>
            <div className={classes.substrateWrapper}>
              <TextFieldLite label="Подложка"
                             required
                             size="small"
                             name="substrate"
                             variant="outlined"
                             value={''}
                             className={classes.substrateField}
                             onChange={() => {
                             }}
                             type='number'/>
              <IconButton className={classes.addButtonMobile}>
                <PlusRoundButton addLine={() => {
                }}/>
              </IconButton>
            </div>
          </div>
        </div>
        <div className={classes.makeOrderBlockLineTwoRight}>
          <div className={classes.makeOrderBlockLineTwoRightOne}>
            <div className={classes.makeOrderBlockLineTwoFirstRightOne}>Стоимость
              начинки
            </div>
            <div className={classes.makeOrderBlockLineTwoFirstRightTwo}>Форма
              торта
            </div>
            <div className={classes.makeOrderBlockLineTwoFirstRightThree}>
              <TextFieldLite label="Цена"
                             required
                             size="small"
                             name="price_two"
                             variant="outlined"
                             value={''}
                             className={classes.productPrice}
                             onChange={() => {
                             }}
                             type='number'/>

              <FormControlLabel
                className={classes.countCheckbox}
                labelPlacement='start'
                control={
                  <PurpleCheckbox
                    checked={showCountAddInfo}
                    onChange={showCountAddInfoHandler}
                    name="priceSecond"
                  />
                }
                label="Рассчитать"
              />
            </div>
          </div>
          {showCountAddInfo && <>
            <div className={classes.makeOrderBlockLineTwoRightTwo}>
              <div className={classes.fillingSelectWrapper}>
                <SelectCustom optionsObject={productFilling}
                              label='Начинка'
                              onChangeFunction={onChangeProductType}
                              val={product.filling}
                              name='filling'
                              className={classes.fillingSelect}
                />
              </div>
              <div className={classes.heightWrapper}>
                <TextFieldLite label="h"
                               required
                               size="small"
                               name="height"
                               variant="outlined"
                               value={''}
                               className={classes.height}
                               onChange={() => {
                               }}
                               type='number'/>
              </div>
              <div className={classes.diameterWrapper}>
                <TextFieldLite label="d"
                               required
                               size="small"
                               name="height"
                               variant="outlined"
                               value={''}
                               className={classes.diameter}
                               onChange={() => {
                               }}
                               type='number'/>
              </div>
            </div>
            <div className={classes.makeOrderBlockLineTwoRightThree}>
              <div className={classes.fillingSelectWrapper}>
                <SelectCustom optionsObject={productFilling}
                              label='Начинка'
                              onChangeFunction={onChangeProductType}
                              val={product.filling}
                              name='filling'
                              className={classes.fillingSelect}
                />
                <IconButton className={classes.addButtonDesktop}>
                  <PlusRoundButton addLine={() => {
                  }}/>
                </IconButton>
              </div>
              <div className={classes.heightWrapper}>
                <TextFieldLite label="h"
                               required
                               size="small"
                               name="height"
                               variant="outlined"
                               value={''}
                               className={classes.height}
                               onChange={() => {
                               }}
                               type='number'/>
                <IconButton className={classes.addButtonMobile}>
                  <PlusRoundButton addLine={() => {
                  }}/>
                </IconButton>
              </div>
              <div className={classes.diameterWrapper}>
              </div>
            </div>
          </>}
        </div>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  makeOrderBlock: {
    border: '1px solid #a7a0a0',
    boxShadow: '0px 10px 15px -5px rgba(0,0,0,0.5)',
    padding: '3%',
    paddingTop: '0',
    flexWrap: 'wrap',
    height: 'fit-content',
    marginTop: '20px',
    ['@media (max-width:1300px)']: {
      flexBasis: '100%',
      maxWidth: 'initial',
      marginBottom: '20px',
    },
    ['@media (max-width:668px)']: {
      border: 'none',
      boxShadow: 'none',
      margin: '0 5px',
    },
  },
  makeOrderBlockLineOne: {
    display: 'flex',
    marginTop: '10px',
    borderBottom: '1px solid #c4bdbd',
    paddingBottom: '20px',
    ['@media (max-width:668px)']: {
      flexWrap: 'wrap'
    },
  },
  productTypeBlock: {
    marginRight: '10px',
    maxWidth: '200px',
    ['@media (max-width:375px)']: {
      maxWidth: '100%',
      marginRight: '0',
    }
  },
  productTypeText: {
    fontSize: '11px',
  },
  createProductFirstLine: {
    display: 'flex',
    alignItems: 'center',
    ['@media (max-width:668px)']: {
      marginTop: '10px',
      display: 'flex',
    },
    ['@media (max-width:375px)']: {
      flexWrap: 'wrap',
      flexDirection: 'column',
      flexBasis: '100%'
    }
  },
  productPrice: {
    marginRight: '8px',
    ['@media (max-width:375px)']: {
      width: '100%',
      marginRight: '0',
    }
  },
  productWeight: {
    marginRight: '8px',
    ['@media (max-width:375px)']: {
      width: '100%',
      marginRight: '0',
    }
  },
  productExpense: {
    ['@media (max-width:375px)']: {
      width: '100%',
      marginRight: '0',
    }
  },
  resFirstLineRedDiv: {
    color: 'red',
  },
  createProductFirstLineResult: {
    textAlign: 'center',
    fontSize: '12px',
    marginRight: '5px',
  },
  countCheckbox: {
    marginLeft: '0',
    '& .MuiTypography-body1': {
      fontSize: '12px',
    },
    ['@media (max-width:375px)']: {
      width: '100%'
    }
  },
  productExpenseWrapper: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    ['@media (max-width:668px)']: {
      flexBasis: '30%'
    },
    ['@media (max-width:375px)']: {
      flexBasis: '100%',
      width: '100%'
    }
  },
  productWeightWrapper: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    ['@media (max-width:668px)']: {
      flexBasis: '30%'
    },
    ['@media (max-width:375px)']: {
      flexBasis: '100%',
      width: '100%'
    }
  },
  productPriceWrapper: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    ['@media (max-width:668px)']: {
      flexBasis: '30%'
    },
    ['@media (max-width:375px)']: {
      flexBasis: '100%',
      width: '100%'
    }
  },
  makeOrderBlockLineTwo: {
    display: 'flex',
    paddingTop: '10px',
    ['@media (max-width:668px)']: {
      flexDirection: 'column'
    },
  },
  makeOrderBlockLineTwoLeft: {
    flexBasis: '100%',
    marginRight: '10px',
    ['@media (max-width:668px)']: {
      marginRight: '0',
    },
  },
  makeOrderBlockLineTwoRight: {
    flexBasis: '100%'
  },
  makeOrderBlockLineTwoLeftOne: {
    display: 'flex'
  },
  makeOrderBlockLineTwoLeftTwo: {
    display: 'flex',
    alignItems: 'center',
    marginTop: '42px',
    marginBottom: '10px',
    ['@media (max-width:668px)']: {
      marginTop: '10px',
    },
    ['@media (max-width:370px)']: {
      flexDirection: 'column',
      alignItems: 'end'
    },
  },
  makeOrderBlockLineTwoLeftThree: {
    display: 'flex',
    ['@media (max-width:370px)']: {
      flexDirection: 'column',
    },
  },
  makeOrderBlockLineTwoRightOne: {
    display: 'flex',
    ['@media (max-width:370px)']: {
      flexDirection: 'column',
    },
  },
  makeOrderBlockLineTwoRightTwo: {
    display: 'flex',
    marginBottom: '10px',
    ['@media (max-width:370px)']: {
      flexDirection: 'column',
    },
  },
  makeOrderBlockLineTwoRightThree: {
    display: 'flex',
    ['@media (max-width:370px)']: {
      flexDirection: 'column',
    },
  },
  makeOrderBlockLineTwoSecond: {
    display: 'flex',
    alignItems: 'center',
    marginTop: '10px',
    justifyContent: 'space-evenly',
  },
  makeOrderBlockLineTwoLeftOneLineFakeOne: {
    display: 'none',
    ['@media (max-width:668px)']: {
      display: 'block',
      flexBasis: '100%',
      marginRight: '4px'
    },
    ['@media (max-width:370px)']: {
      display: 'none',
    },
  },
  decorationWeight: {
    ['@media (max-width:370px)']: {
      width: '100%',
    },
  },
  makeOrderBlockLineTwoLeftOneLineFakeTwo: {
    display: 'none',
    ['@media (max-width:668px)']: {
      display: 'block',
      flexBasis: '100%',
    },
    ['@media (max-width:370px)']: {
      display: 'none',
    },
  },
  makeOrderBlockLineTwoLeftOneLineWeightLine: {
    flexBasis: '100%',
    marginRight: '7px',
    ['@media (max-width:370px)']: {
      marginRight: '0',
    },
  },
  makeOrderBlockLineTwoThird: {
    display: 'flex',
    alignItems: 'end',
    marginTop: '10px',
    justifyContent: 'space-evenly',
  },
  boxSizeWrapper: {
    flexBasis: '100%',
    marginRight: '10px',
    ['@media (max-width:370px)']: {
      width: '100%',
      marginBottom: '10px'
    },
  },
  boxSizeField: {
    ['@media (max-width:370px)']: {
      width: '100%',
    },
  },
  substrateWrapper: {
    flexBasis: '100%',
    ['@media (max-width:370px)']: {
      width: '100%',
    },
  },
  substrateField: {
    ['@media (max-width:370px)']: {
      width: '100%',
    },
  },
  makeOrderBlockLineTwoSecondTitle: {
    flexBasis: '100%',
    marginRight: '7px',
    ['@media (max-width:370px)']: {
      marginBottom: '5px'
    },
  },
  makeOrderBlockLineTwoThirdTitle: {
    marginRight: '7px',
    minWidth: '59px',
    flexBasis: '100%',
    ['@media (max-width:370px)']: {
      marginTop: '10px',
      marginBottom: '10px',
    },
  },
  addButtonMobile: {
    ['@media (min-width:370px)']: {
      display: 'none',
    },
  },
  addButtonDesktop: {
    ['@media (max-width:370px)']: {
      display: 'none',
    },
  },
  heightWrapper: {
    flexBasis: '12%',
    marginRight: '10px',
    ['@media (max-width:370px)']: {
      width: '50%',
      marginBottom: '10px',
    },
  },
  diameterWrapper: {
    flexBasis: '12%',
    ['@media (max-width:370px)']: {
      width: '50%',
      marginBottom: '10px',
    },
  },
  extraExpensesTitleWrapper: {
    flexBasis: '100%',
    textAlign: 'right',
    marginRight: '10px',
    ['@media (max-width:370px)']: {
      marginRight: '0',
      marginBottom: '10px'
    },
  },
  extraExpensesTitle: {
    width: '100%',
  },
  makeOrderBlockLineTwoFirst: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  makeOrderBlockLineTwoFirstRight: {
    flexBasis: '50%',
    display: 'flex',
    justifyContent: 'space-between',
  },
  makeOrderBlockLineTwoFirstRightOne: {
    flexBasis: '35%',
    marginRight: '15px',
    ['@media (max-width:370px)']: {
      marginBottom: '10px',
      marginTop: '15px'
    },
  },
  makeOrderBlockLineTwoFirstRightTwo: {
    flexBasis: '35%',
    ['@media (max-width:370px)']: {
      marginBottom: '20px',
      marginTop: '10px'
    },
  },
  makeOrderBlockLineTwoFirstRightThree: {
    flexBasis: '28%',
  },
  fillingSelectWrapper: {
    textAlign: 'right',
    marginRight: '10px',
    ['@media (max-width:370px)']: {
      marginTop: '10px',
      marginBottom: '10px',
      marginRight: '0',
    },
  },
}));

export default MakeOrderMakeOrder;
